}

.about {
    @include xs-block {
      position: relative;
      padding-bottom: rem(120);
    }
    @include lg-block {
      height: 100vh;
      position: relative;
      margin-bottom: 0px;
    }
    .content {

      @include xs-block() {
        height: max-content;
      }
      @include lg-block {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: space-between;
        margin-top: rem($mr_from_header);
      }


      .description {
        position: relative;
       
        @include size-xs(13);
        @include size-lg(6);
        @include xs-block {
          display: flex;
          flex-flow: nowrap column;
          justify-content: flex-start;
          align-items: center;
          height: 100%;
        }
        @include lg-block {
          align-items: flex-start;
        }
        @include xl-block {
          position: static;
          align-items: flex-start;
        }
        .title {
          @include xs-block{
            margin-top: 0;
          }
  
          }
    
        .text {
          @include xs-block {
            margin-bottom: rem(40);
          }
        }
      }

  .viwe {
    height: 110vh;
    background: #0c1723;
    z-index: -1;
    border-bottom: none;
    border: rem(25) solid #eeeeee;

    @include xs(display, none);
    @include size-md(12);
    @include size-lg(5);
    @include size-xl(6);

    @include lg-block {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: rem(400);
      position: absolute;
      top: 3vh;
      left: 49.6%;
      border-radius: rem(68);
    }
    @include xl-block {
      position: absolute;
      top: 3vh;
      left: 52.7%;
      border-radius: rem(88);
    }

    .top_of_viwe {
      border-bottom: rem(2) solid #242e39;

      @include lg-block {
        margin: 0 rem(20);
        padding: rem(25) 0 rem(30) 0;
      }

      @include xl-block {
        margin: 0 rem(40);
        padding: rem(37) 0 rem(39) 0;
      }

      .head {
        position: relative;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        color: white;
        box-shadow: 0px rem(-10) rem(28) rgba(0, 0, 0, 0.06);
        background: no-repeat url('../img/svg/steve.svg'), rgba(255, 255, 255, 0.05);
        background-size: 15%;
        @include lg-block {
          width: rem(300);
          border-radius: rem(22);
          height: rem(90);
          background-position: rem(10) center;
        }

        @include xl-block {
          width: rem(421);
          height: rem(100);
          border-radius: rem(28);
          background-position: rem(22) center;
        }

        &:after {
          content: '';
          position: absolute;
          bottom: rem(25);
          left: rem(80);
          background: no-repeat url('../img/svg/stars.svg');
          background-size: contain;
          width: rem(107);
          height: rem(15);

          @include lg-block {
            left: rem(77);
          }

          @include xl-block {
            left: rem(96);
          }
        }

        .steve_name {
          line-height: rem(30);
          height: rem(53);
          border-right: rem(2) solid rgba(255, 255, 255, 0.1);

          @include lg-block {
            padding-left: rem(65);
            padding-right: rem(10);
            font-size: rem(16);
          }

          @include xl-block {
            font-size: rem(20);
            padding-left: rem(70);
            padding-right: rem(30);
          }
        }

        .per {
          text-transform: uppercase;
          margin-bottom: 0px;

          @include lg-block {
            font-size: rem(16);
          }

          @include xl-block {
            font-size: rem(20);
          }

          p {
            color: #9297a2;
            text-transform: none;
            margin: 0;
            margin-top: rem(10);

            @include lg-block {
              font-size: rem(12);
              margin-left: rem(3);
            }
            @include xl-block {
              margin-left: rem(5);
              font-size: rem(14);
            }
          }
        }
      }
    }

    .chat {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      color: white;
      font-weight: 300;

      @include lg-block {
        transform: translateY(rem(352));
        width: calc(100% - 40px);
        font-size: rem(14);
        animation: move_chat_lg 20s 3s linear forwards;
      }
      @include xl-block {
        transform: translateY(rem(402));
        width: calc(100% - 3.6875rem);
        font-size: rem(16);
        animation: move_chat 20s 3s linear forwards;
      }

      li {
        @include chat_message();
        animation: show_message 0.5s linear forwards;
        opacity: 0;

        &:nth-child(1) {
          animation-delay: 1.5s;
        }
        &:nth-child(2) {
          animation-delay: 4s;
        }
        &:nth-child(3) {
          animation-delay: 7s;
        }
        &:nth-child(4) {
          animation-delay: 10s;
        }
        &:nth-child(5) {
          animation-delay: 13s;
        }
        &:nth-child(6) {
          animation-delay: 16s;
        }
      }
    }
    .user {
      align-self: flex-end;
      background: #0072ff;
      display: inline-block;
    }

    .steve {
      background: rgba(255, 255, 255, 0.05);
      align-self: flex-start;
    }

    .steve_typing {
      @include typing();
      animation: show_typing_steve 20s linear forwards;

      @include lg-block {
        left: rem(20);
        top: rem(600);
      }

      @include xl-block {
        left: rem(40);
        top: rem(650);
      }
    }

    .user_typing {
      @include typing();
      animation: show_typing_user 20s linear forwards;

      @include lg-block {
        right: rem(20);
        top: rem(600);
      }

      @include xl-block {
        right: rem(40);
        top: rem(650);
      }
    }
  }

  .section_link {
    @include xs-block {
      position: absolute;
      bottom: 70px;
    }
    @include sm-block {
      bottom: 50px;
    }
    @include lg-block {
      position: absolute;
      display: flex;
      bottom: rem(70);
      margin-bottom: 0;
      width: 100%;
    }
    @include xl-block {
      bottom: rem(90);
    }
    @media screen and (max-height: 800px) and (min-width: 1200px) {
      bottom: rem(60);
    }
  }  
  }
}

