    .viwe {
      box-sizing: border-box;
      box-sizing: border-box;
      display: flex;
      flex-direction: column;
      align-items: center;

      @include xs-block {
        position: relative;
        width: 100%;
        overflow: visible;
        left: 0;
        border: none;
