      @include size-xl(6);
      @include xl-block {
        padding: 0;
        border-radius: 0px 0px rem(80) rem(80);
        left: 52.7%;
        top: rem(-325);
      }
      @media screen and (min-width: 1200px)  and (max-height: 800px) {
        top: rem(-325);
      }

    

      .viwe__title {
        font-weight: bold;
        border-bottom: solid rem(1) #242e39;

        @include xs-block {
          box-sizing: border-box;
          text-align: center;
          padding: 39px 70px;
          font-size: 28px;
          width: 315px;
        }
        @include sm-block {
          @include size-sm(12);
          font-size: 44px;
        }
        @include lg-block {
          text-align: center;
          padding: rem(20) 0 rem(39) 0;
          font-size: rem(28);
          width: rem(300);
          margin: rem(22) rem(35) 0;
        }
        @include xl-block {
          text-align: center;
          padding: rem(39) 0;
          width: rem(420);
          margin: rem(85) rem(35) 0;
        }
      }

      .featches {
        padding-top: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: rem(60);

        @include xs-block {
          width: 315px;
        }

        @include size-sm(12);
        @include lg-block {
          width: rem(300);
        }
        @include size-xl(12);

        .feache__item {
          box-sizing: border-box;
          color: #000;
          margin-bottom: 4.9vh;
          position: relative;
          border-radius: rem(28);

          @include xs-block {
            width: 100%;
            padding: 25px 0 25px 100px;
            margin-bottom: 55px;
          }
          @include lg-block {
            height: rem(100);
            padding: rem(15) 0 rem(25) rem(100);
            margin-bottom: rem(40);
            opacity: 0;
          }
          @include size-xl(12);
          @include xl-block {
            padding: rem(25) 0 rem(25) rem(100);
            opacity: 0;
          }

          &:nth-child(1) {
            background: no-repeat url('../img/svg/ic.svg'), #ffffff;
            @include feache_bg();
            &::before {
              content: '1';
              @include feach_icon;
            }
          }
          &:nth-child(2) {
            background: no-repeat url('../img/svg/ic2.svg'), #ffffff;
            @include feache_bg();

            &::before {
              content: '2';
              @include feach_icon;
            }
          }
          &:nth-child(3) {
            background: no-repeat url('../img/svg/ic3.svg'), #ffffff;
            @include feache_bg();

            &::before {
              content: '3';
              @include feach_icon;
            }
          }

          h4 {
            font-size: rem(20);
          }

          p {
            margin: rem(10) 0 0 0;
            font-size: rem(14);
            color: #9297a2;
          }
        }
      }
    }
  }

  .section_link {
