      padding-left: rem(40);
    

      @include xs-block {
        display: inline-block;
        width: 100%;
        background-position: rem(13);
      }
    }
  }
}

#download {
  position: relative;
  @include lg-block {
    height: 100vh;
    background: no-repeat url('../img/svg/main_bg.svg');
    background-position: 4vw -29vh;
    background-size: rem(1400);
  }
  
  @media screen and (max-height: 800px) and (min-width: 992px) {
    background-position: 16vw -29vh;
    
  }
    
  
  @include xl-block {
    background-size: rem(1700);
  }


  @media screen and (min-width: 1920px) {
    background-size: rem(1570);
  }

  $space_x: 150;
  $space_y: 320;
  $width: 1200;
  @for $i from 0 through 8 {
    @media screen and (min-width: rem($width + ($i * 100))) {
      background-position-x: rem($space_x + ($i * 55));
      background-position-y: rem(-$space_y);
    }
  }

  @for $i from 0 through 8 {
    $space_x_1: 450;
    @media screen and (max-height: 800px) and (min-width: rem($width + ($i * 100))) {
      background-size: rem(1470);
      background-position-x: rem($space_x_1 + ($i * 60));
      background-position-y: rem(-$space_y);
    }
  }
}

